import React, { useEffect, useState } from "react";
import {
  ComponentProps,
  Streamlit,
  withStreamlitConnection,
} from "streamlit-component-lib";
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';
import Slide from '@mui/material/Slide';
import Fade from '@mui/material/Fade';
import Grow from '@mui/material/Grow';
import Zoom from '@mui/material/Zoom';
import { TransitionProps } from '@mui/material/transitions';

export interface DialogTitleProps {
  id: string;
  children?: React.ReactNode;
  onClose: () => void;
  sx?: any;
}

function BootstrapDialogTitle(props: DialogTitleProps) {
  const { children, onClose, sx, ...other } = props;

  return (
    <DialogTitle sx={sx} {...other}>
      {children}
      {onClose ? (
        <IconButton
          aria-label="close"
          onClick={onClose}
          sx={{
            position: 'absolute',
            right: 8,
            top: 8,
            color: (theme) => theme.palette.grey[500],
          }}
        >
          <CloseIcon />
        </IconButton>
      ) : null}
    </DialogTitle>
  );
}


export interface TransitionProbsCustom {
  transition_mode: any;
  slide_direction: any;
}


const Transition_zoom = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Zoom ref={ref} {...props} />;
});

const Transition_fade = React.forwardRef(function Transition(	
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Fade ref={ref} {...props} />;
});

const Transition_grow = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Grow ref={ref} {...props} />;
});


const Transition_slide_down = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="down" ref={ref} {...props} />;
});

const Transition_slide_up = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const Transition_slide_left = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="left" ref={ref} {...props} />;
});

const Transition_slide_right = React.forwardRef(function Transition(
  props: TransitionProps & {
    children: React.ReactElement<any, any>;
  },
  ref: React.Ref<unknown>,
) {
  return <Slide direction="right" ref={ref} {...props} />;
});


const DialogComponent = (props: ComponentProps) => {
  const [open, setOpen] = React.useState(false);

  const {title, content, abortlabel, agreelabel, height, fullscreen, styling_dialog, button_txt, button_open_type,
    divider, styling_open_button, styling_agree_button, styling_abort_button, width_dialog, adapt_width_dialog,
    styling_dialog_content, styling_dialog_title, styling_dialog_content_text, transition_mode, slide_direction,
  button_abort_type, button_agree_type, on_agree, on_abort, on_close, on_open} = props.args;

  const sx_dialog_json = JSON.parse(styling_dialog);
  const sx_open_btn_json = JSON.parse(styling_open_button);
  const sx_agree_btn_json = JSON.parse(styling_agree_button);
  const sx_abort_btn_json = JSON.parse(styling_abort_button);
  const sx_dialog_content_json = JSON.parse(styling_dialog_content);
  const sx_dialog_title_json = JSON.parse(styling_dialog_title);
  const sx_dialog_content_text_json = JSON.parse(styling_dialog_content_text);

  if (on_agree) {
    var on_agree_func = Function(on_agree);
  }
  else {
    var on_agree_func = Function();
  }

  if (on_abort) {
    var on_abort_func = Function(on_abort);
  }
  else {
    var on_abort_func = Function();
  }

  if (on_close) {
    var on_close_func = Function(on_close);
  }
  else {
    var on_close_func = Function();
  }

  if (on_open) {
    var on_open_func = Function(on_open);
  }
  else {
    var on_open_func = Function();
  }


  const handleClickOpen = () => {
    setOpen(true);
    on_open_func();
  };

  const handleClose = () => {
    setOpen(false);
    on_close_func();
  };

  //if open is true, then set the frame height to 500 else
  //set the frame height to 100

  useEffect(() => {
    if (open) {
      Streamlit.setFrameHeight(height);
    } else {
      Streamlit.setFrameHeight();
    }
  }, [open]);

  //choose right transition function and save it in transition_func
  var transition_func = Transition_zoom;
  if (transition_mode == "zoom") {
    var transition_func = Transition_zoom;
  }
  else if (transition_mode == "fade") {
    var transition_func = Transition_fade;
  }
  else if (transition_mode == "grow") {
    var transition_func = Transition_grow;
  }
  else if (transition_mode == "slide") {
    if (slide_direction == "down") {
      var transition_func = Transition_slide_down;
    }
    else if (slide_direction == "up") {
      var transition_func = Transition_slide_up;
    }
    else if (slide_direction == "left") {
      var transition_func = Transition_slide_left;
    }
    else if (slide_direction == "right") {
      var transition_func = Transition_slide_right;
    }
  }


  
  return (
    <div>
      <Button color={button_open_type} variant="outlined" onClick={handleClickOpen} sx= {sx_open_btn_json}>
        {button_txt}
      </Button>
      <Dialog
        fullScreen={fullscreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        sx= {sx_dialog_json}
        maxWidth={width_dialog}
        fullWidth={adapt_width_dialog}
        TransitionComponent={transition_func}
      >
    <BootstrapDialogTitle id="alert-dialog-title" onClose={handleClose} sx = {sx_dialog_title_json}>
      {title}
      </BootstrapDialogTitle>
        <DialogContent dividers = {divider} sx = {sx_dialog_content_json}>
          <DialogContentText id="alert-dialog-description" sx = {sx_dialog_content_text_json}>
          {content}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => {handleClose(); Streamlit.setComponentValue(false); on_abort_func();}} 
          sx = {sx_abort_btn_json} color = {button_abort_type}>{abortlabel}</Button>
          <Button onClick={() => {handleClose(); Streamlit.setComponentValue(true); on_agree_func();}} sx = {sx_agree_btn_json} autoFocus color = {button_agree_type}>
            {agreelabel}
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
};


export default withStreamlitConnection(DialogComponent);
